:root,
:root.light{
    --bg-base:#ECE3D4;
    --bg-footer:#E3D6C3;
    --border:#C7A26B;
    --title-font:#372A15;
    --title-sub-font:#5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151
}

:root.dark{
    --bg-base:#333333;
    --border:#444444;
    --bg-color3:#555555;
    --bg-color4:#666666;
    --bg-color5:#777777;
}

body{
    background-color: var(--bg-base);
    color: var(--bg-color5);
}
.bg-transition{
    transition: background-color 0.3s ease, color 0.3s ease;
}
